/* eslint-disable no-useless-escape */
import React, { useState } from "react";
import styled from "styled-components";
import {
  FormInputField,
  FormSelectField,
  Form,
  FormStrategy,
  Validators,
  Button,
  Notify
} from "zent";

import { addUser } from "../../services/userService";

const AddUser = ({ history }) => {
  const form = Form.useForm(FormStrategy.View);
  const [isLoading, setLoading] = useState(false);

  const submit = () => {
    setLoading(true);
    const {
      fullName,
      email,
      mobileNumber,
      username,
      password,
      role
    } = form.getValue();
    addUser({
      fullName,
      email,
      mobileNumber,
      username,
      password,
      role,
      confirmed: true
    })
      .then(res => {
        Notify.success("      .", 4000);
        return history.replace("/users");
      })
      .catch(err =>
        Notify.error("        .", 4000)
      );
  };

  return (
    <Container className="animated fadeIn">
      <h1> </h1>
      <Form
        layout={"vertical"}
        form={form}
        onSubmit={submit}
        disableEnterSubmit={false}
      >
        <div className="zent-form-row">
          <FormInputField
            name="fullName"
            label="   "
            validateOccasion={
              Form.ValidateOccasion.Blur | Form.ValidateOccasion.Change
            }
            validators={[
              Validators.required("      .")
            ]}
            required="Required"
          />
          <FormInputField
            name="email"
            label=""
            validateOccasion={
              Form.ValidateOccasion.Blur | Form.ValidateOccasion.Change
            }
            validators={[
              Validators.required("   ."),
              Validators.email("      .")
            ]}
            required="Required"
          />
          <FormInputField
            name="mobileNumber"
            label=" "
            props={{
              type: "tel"
            }}
            validateOccasion={
              Form.ValidateOccasion.Blur | Form.ValidateOccasion.Change
            }
            validators={[
              Validators.required("    ."),
              Validators.pattern(/^([0-9\(\)\/\+ \-]*)$/)
            ]}
            required="Required"
          />
        </div>
        <div className="zent-form-row">
          <FormInputField
            name="username"
            label=" "
            validateOccasion={
              Form.ValidateOccasion.Blur | Form.ValidateOccasion.Change
            }
            validators={[Validators.required("    .")]}
            required="Required"
          />
          <FormInputField
            name="password"
            label=" "
            props={{
              minLength: 6,
              type: "password"
            }}
            validateOccasion={
              Form.ValidateOccasion.Blur | Form.ValidateOccasion.Change
            }
            validators={[
              Validators.required("    ."),
              Validators.minLength("     .")
            ]}
            // helpDesc="      ."
            required="Required"
          />
          <FormSelectField
            name="role"
            label=" "
            props={{
              placeholder: "    ",
              data: [
                { value: "5e43aad2763550c66de25847", text: "" },
                { value: "5e4c5e5860810adcb25ae728", text: "" }
              ],
              autoWidth: true
            }}
            validateOccasion={
              Form.ValidateOccasion.Blur | Form.ValidateOccasion.Change
            }
            validators={[Validators.required("    .")]}
            required="Required"
          />
        </div>
        <Button htmlType="submit" type="primary" loading={isLoading}>
          
        </Button>
      </Form>
    </Container>
  );
};

const Container = styled.div`
  background-color: #fff;
  padding: 35px 35px 35px 20px;
  border-radius: 6px;
  h1 {
    font-size: 2em;
    font-weight: bold;
    color: #222;
    margin-bottom: 30px;
  }
`;

export default AddUser;
