import React, { Component } from "react";
import { Grid, Notify, Button, Input, Sweetalert } from "zent";

import { fetchUsers, deleteUser } from "../../services/userService";
import Block from "../../components/common/block";

class Products extends Component {
  constructor(props) {
    super(props);
    this.state = {
      datasets: [],
      isLoading: false,
    };
  }

  componentDidMount() {
    this.fetchData();
  }

  fetchData = (query = "") => {
    fetchUsers(query)
      .then((res) => {
        this.setState({ datasets: res.data });
      })
      .catch((err) =>
        Notify.error(
          "          ."
        )
      );
  };

  onChange = (conf) => {
    console.log(conf, "conf");
    // const { sortType, sortBy } = conf;
    // const { datasets } = this.state;
    // let sortDatasets = datasets;
    // if (sortType === 'asc') {
    //   sortDatasets = datasets.sort((a, b) => a[sortBy] - b[sortBy]);
    // } else if (sortType === 'desc') {
    //   sortDatasets = datasets.sort((a, b) => b[sortBy] - a[sortBy]);
    // }
    // this.setState(assign({}, this.state, conf, { datasets: sortDatasets }));
  };

  removeUser = (id) => {
    Sweetalert.confirm({
      content: `      `,
      title: ``,
      confirmType: "danger",
      confirmText: ``,
      cancelText: ``,
      onConfirm: () =>
        new Promise((resolve) => {
          deleteUser(id)
            .then(() => {
              this.fetchData();
              Notify.success("    .", 5000);
              return resolve();
            })
            .catch((err) => {
              Notify.error("       .", 5000);
              return resolve();
            });
        }),
    });
  };

  onChangeSearch = (e) => {
    if (!e.target.value && e.target.value.trim() !== "") {
      return this.fetchData(e.target.value)
        .then((res) => {
          this.setState({ datasets: res.data });
        })
        .catch((err) =>
          Notify.error(
            "          ."
          )
        );
    }
  };

  onPressEnter = (e) => {
    fetchUsers(e.target.value)
      .then((res) => {
        this.setState({ datasets: res.data });
      })
      .catch((err) =>
        Notify.error(
          "          ."
        )
      );
  };

  renderSize = (item) => {
    switch (Number(item)) {
      case 1:
        return "XS";
      case 2:
        return "S";
      case 3:
        return "M";
      case 4:
        return "L";
      case 5:
        return "XL";
      case 6:
        return "XXL";
      default:
        return "";
    }
  };

  render() {
    const { datasets, isLoading } = this.state;
    const { history } = this.props;
    const columns = [
      {
        title: "   ",
        name: "fullName",
      },
      {
        title: " ",
        name: "mobileNumber",
      },
      {
        title: "",
        name: "email",
      },
      {
        title: " ",
        bodyRender: (data) => {
          return data.role.type === "authenticated" ? "" : "";
        },
      },
      {
        title: "",
        bodyRender: (data) => {
          return (
            <div className="table-control__container">
              <Button
                type="primary"
                onClick={() => history.push(`/user/${data.id}`)}
              >
                
              </Button>
              <Button type="danger" onClick={() => this.removeUser(data.id)}>
                
              </Button>
            </div>
          );
        },
      },
    ];
    return (
      <div className="animated fadeIn">
        <Block>
          <h1 className="title"> </h1>

          <div className="row">
            <Input
              onPressEnter={this.onPressEnter}
              onChange={this.onChangeSearch}
              icon="search"
              placeholder=" ..."
            />
            <Button onClick={() => history.push("/user/add")}> </Button>
          </div>
        </Block>
        <Grid
          columns={columns}
          datasets={datasets}
          onChange={this.onChange}
          emptyLabel={"    ."}
          loading={isLoading}
        />
      </div>
    );
  }
}

export default Products;
